___CSS_LOADER_EXPORT___.push([module.id, `header {
    display: inline;
}

header nav {
    display: flex;
    background-color: #F5EBE0;
    justify-content: space-around;
    height: 60px;
    align-items: center;
    color: #DBA39A;
}

header nav ul {
    display: flex;
    list-style: none;
    width: 15%;
    justify-content: space-between;
}

header nav ul li a {
    color: #DBA39A;
    text-decoration: none;
    padding: 13px 0;
}

header nav ul li a:hover {
    color: #000;
    font-weight: bold;
}

header nav .logo {
    font-family: 'Lobster Two', sans-serif;
    font-size: 30px;
    letter-spacing: 2px;
    z-index: 1;
}

header nav .hamburger-menu {
    display: none;
    flex-direction: column;
    height: 20px;
    justify-content: space-between;
    position: relative;
    z-index: 1;
}

header nav .hamburger-menu input {
    position: absolute;
    width: 44px;
    height: 44px;
    top: -11px;
    left: -7px;
    z-index: 2;
    cursor: pointer;
    background: rgba(40, 40, 40, 0);
    -webkit-appearance: none;
    appearance: none;
}

header nav .hamburger-menu span {
    display: block;
    width: 28px;
    height: 3px;
    background-color: #DBA39A;
    border-radius: 3px;
    transition: all 0.5s;
}

@media screen and (max-width: 768px) {
    header nav ul {
        width: 50%;
    }
}

@media screen and (max-width: 1440px) {
    header nav ul {
        width: 25%;
    }
}

@media screen and (max-width: 1024px) {
    header nav ul {
        width: 30%;
    }
}

@media screen and (max-width: 700px) {
    header nav .hamburger-menu {
        display: flex;
    }

    header nav ul {
        position: absolute;
        right: 0;
        height: 100vh;
        width: 100%;
        top: 0;
        align-items: center;
        justify-content: space-evenly;
        flex-direction: column;
        background-color: #F5EBE0;
        transform: translateX(100%);
        opacity: 0;
    }

    header nav ul.slide {
        transform: translateX(0);
        opacity: 1;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/navbar.css"],"names":[],"mappings":"AAAA;IACI,eAAe;AACnB;;AAEA;IACI,aAAa;IACb,yBAAyB;IACzB,6BAA6B;IAC7B,YAAY;IACZ,mBAAmB;IACnB,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,gBAAgB;IAChB,UAAU;IACV,8BAA8B;AAClC;;AAEA;IACI,cAAc;IACd,qBAAqB;IACrB,eAAe;AACnB;;AAEA;IACI,WAAW;IACX,iBAAiB;AACrB;;AAEA;IACI,sCAAsC;IACtC,eAAe;IACf,mBAAmB;IACnB,UAAU;AACd;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,YAAY;IACZ,8BAA8B;IAC9B,kBAAkB;IAClB,UAAU;AACd;;AAEA;IACI,kBAAkB;IAClB,WAAW;IACX,YAAY;IACZ,UAAU;IACV,UAAU;IACV,UAAU;IACV,eAAe;IACf,+BAA+B;IAC/B,wBAAwB;IACxB,gBAAgB;AACpB;;AAEA;IACI,cAAc;IACd,WAAW;IACX,WAAW;IACX,yBAAyB;IACzB,kBAAkB;IAClB,oBAAoB;AACxB;;AAEA;IACI;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,UAAU;IACd;AACJ;;AAEA;IACI;QACI,aAAa;IACjB;;IAEA;QACI,kBAAkB;QAClB,QAAQ;QACR,aAAa;QACb,WAAW;QACX,MAAM;QACN,mBAAmB;QACnB,6BAA6B;QAC7B,sBAAsB;QACtB,yBAAyB;QACzB,2BAA2B;QAC3B,UAAU;IACd;;IAEA;QACI,wBAAwB;QACxB,UAAU;IACd;AACJ","sourcesContent":["header {\r\n    display: inline;\r\n}\r\n\r\nheader nav {\r\n    display: flex;\r\n    background-color: #F5EBE0;\r\n    justify-content: space-around;\r\n    height: 60px;\r\n    align-items: center;\r\n    color: #DBA39A;\r\n}\r\n\r\nheader nav ul {\r\n    display: flex;\r\n    list-style: none;\r\n    width: 15%;\r\n    justify-content: space-between;\r\n}\r\n\r\nheader nav ul li a {\r\n    color: #DBA39A;\r\n    text-decoration: none;\r\n    padding: 13px 0;\r\n}\r\n\r\nheader nav ul li a:hover {\r\n    color: #000;\r\n    font-weight: bold;\r\n}\r\n\r\nheader nav .logo {\r\n    font-family: 'Lobster Two', sans-serif;\r\n    font-size: 30px;\r\n    letter-spacing: 2px;\r\n    z-index: 1;\r\n}\r\n\r\nheader nav .hamburger-menu {\r\n    display: none;\r\n    flex-direction: column;\r\n    height: 20px;\r\n    justify-content: space-between;\r\n    position: relative;\r\n    z-index: 1;\r\n}\r\n\r\nheader nav .hamburger-menu input {\r\n    position: absolute;\r\n    width: 44px;\r\n    height: 44p